body {
  margin: 0;
  padding: 0;
  font-family: sans-serif; }

.App {
  text-align: center;
  font-family: 'sans-serif'; }

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px; }

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white; }

.App-title {
  font-size: 1.5em; }

.App-intro {
  font-size: large; }

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

.content {
  display: flex; }

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  width: 100%;
  border-bottom: 1px #5185e9 solid;
  background-color: #FBFBFB;
  color: #5185e9;
  font-weight: 600; }

.button {
  border-radius: 5px;
  padding: 1em;
  margin: 2em 0;
  width: 100%;
  font-weight: 600;
  color: #5185e9;
  background-color: #FBFBFB;
  border: 1px #5185e9 solid; }
  .button:active {
    outline: none; }
  .button:focus {
    outline: none; }
  .button:hover {
    color: #FDFDFD;
    background-color: #5185e9;
    border: 1px #FBFBFB solid;
    cursor: pointer; }
  .button--small {
    max-width: 15em; }
  .button--medium {
    max-width: 25em; }
  .button-label--disabled {
    background-color: #E3E3E3;
    color: #FBFBFB;
    border: 1px #E3E3E3 solid; }
    .button-label--disabled:hover {
      cursor: not-allowed;
      background-color: #E3E3E3;
      color: #FBFBFB;
      border: 1px #E3E3E3 solid; }
  .button-green {
    border: 1px #3fea76 solid;
    color: #3fea76; }
    .button-green:hover {
      color: #FDFDFD;
      background-color: #3fea76;
      border: 1px #FBFBFB solid;
      cursor: pointer; }

.sidebar {
  background-color: #FBFBFB;
  border-left: 1px #5185e9 solid;
  padding: 0 1em;
  width: 200px;
  height: 100vh; }
  .sidebar__max-selection {
    border-radius: 5px;
    padding: .5em;
    margin: .25em 0 1em;
    font-size: 1em;
    box-sizing: border-box;
    min-width: 100%;
    font-weight: 600;
    color: #5185e9;
    background-color: #FBFBFB;
    border: 1px #5185e9 solid; }
    .sidebar__max-selection:active {
      outline: none; }
    .sidebar__max-selection:focus {
      outline: none; }
    .sidebar__max-selection--disabled {
      background-color: #E3E3E3;
      color: #FBFBFB;
      border: 1px #E3E3E3 solid; }
  .sidebar__selected-count {
    padding: 1em 0;
    font-weight: 600;
    color: #5185e9; }
  .sidebar__labeled-count {
    padding: 1em 0;
    font-weight: 600;
    color: #5185e9; }
  .sidebar__axis {
    background-color: #FBFBFB;
    display: block;
    width: 100%;
    border: 1px #5185e9 solid;
    border-radius: 5px;
    color: #5185e9;
    height: 3em;
    font-weight: 600;
    margin: .25em 0 2em; }
    .sidebar__axis:active {
      outline: none; }
    .sidebar__axis:focus {
      outline: none; }
  .sidebar__label {
    color: #5185e9;
    font-weight: 500;
    padding-top: 1em; }

.content {
  display: flex;
  justify-content: space-between; }

.modal__text-results {
  white-space: pre; }

.graph-container {
  padding: 0 3em;
  align-items: center; }



